(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($window, $scope, $log, $state, authService, toastr, $location, $anchorScroll) {
      var vm = this,
          user = angular.copy(authService.getUser());

      vm.userImg = './assets/images/jasmine.png';

      if(!user) {
        $state.go('login');
      } else {
        vm.token = user.token;
        vm.userImg = user.u_picture;
        vm.$state = $state;
        authService.authorizationStatus(user.token).then(function(res){
          if(res.data.ppulse.status==='failed'){
            toastr.warning('Your Session has expired!');
            $state.go('login');
          }
        }).catch(function(err){
          $log.log(err);
        });
      }

      vm.logout = function() {
        authService.logout(user.token).then(function(res){
            $state.go('login');
        }).catch(function(err){
          $log.log(err);
        });
      };

      $scope.$on('$stateChangeStart', function(){
          console.log('change Start');
          $anchorScroll();
      });

      $scope.$on('$stateChangeSuccess', stateChangeSuccess);

      function stateChangeSuccess() {
        // Collapsing the menu after navigation
        // vm.isCollapsed = false;
        user = angular.copy(authService.getUser());
        
        if(!user) {
          $state.go('login');
        } else {

          if(arguments[1].name !== 'login'){
            authService.authorizationStatus(user.token).then(function(res){
              if(res.data.ppulse.status==='failed'){
                toastr.warning('Your Session has expired!');
                $state.go('login');
              }
            }).catch(function(err){
              $log.log(err);
            });
          }
        }
      }
    }
  }

})();

(function () {
  angular.module('ppulse')
    .service('CommanService', function ($http, config) {
      var queryOptions = null;
      var allAlerts = null;

      this.setAlertdata = function(alerts) {
        allAlerts = alerts;
      };

      this.setQuery = function (query) {
        queryOptions = query;
      };

      this.getAlertName = function(token, alertId){
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/getAlertName.php',
          data: {
            alertid : alertId },
          headers: {
            'access-token' : token
          }
        };
        return $http(req);
      };

      this.getQuery = function () {
        return queryOptions;
      };

      this.getAlerts = function (token) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/getAlertStats.php',
          headers: {
            'access-token' : token
          }
        };
        return $http(req);
      };

      this.getCompanies = function (query) {
        var req = {
          method: 'get',
          url: 'https://autocomplete.clearbit.com/v1/companies/suggest?query=' + query
        };
        return $http(req);
      };

      this.getLanguages = function () {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/getLanguages.php'
        };
        return $http(req);
      };

      this.getSources = function () {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/getSources.php'
        };
        return $http(req);
      };

      this.getCities = function () {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/getCities.php'
        };
        return $http(req);
      };

    });
})();

(function () {
  angular.module('ppulse')
    .service('authService', function ($http, config,$localStorage) {
      var user = {};
      this.login = function (credentials) {
        return $http.post(config.apiUrl + '/user/auth', credentials);
      };

      this.logout = function (token) {
        $localStorage.user = undefined;
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/logout.php',
          headers: {
            'access-token' : token
          }
        };
        return $http(req);
      };

      this.authorizationStatus = function (token) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/checkLogin.php',
          headers: {
            'access-token' : token
          }
        };
        return $http(req);
      };

      this.activteUser = function (credentials) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/verifyUserEmail.php',
          data: credentials
        };
        return $http(req);
      };

      this.forgotPass = function (credentials) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/forgotPassword.php?email='+credentials.username,
          data: credentials
        };
        return $http(req);
      };

      this.setUser = function(userDetails) {
        $localStorage.user = angular.copy(userDetails);
      };

      this.getUser = function() {
        return $localStorage.user;
      };
    });
})();

